import React from 'react';
import ReactDOM from 'react-dom';

import OneTab from './onetab';
import TwoTab from './twotab';
import ThreeTab from './threetab';
import FourTab from './fourtab';

const Classifier=(props)=>{
    console.log('HelloClassidifie'+props.value)
    return(
        (props.value===0)?<OneTab changeZ={props.changeZ} revertZ={props.revertZ}/>:
                    (props.value===1)?<TwoTab changeZ={props.changeZ} revertZ={props.revertZ}/>:
                        (props.value===2)?<ThreeTab changeZ={props.changeZ} revertZ={props.revertZ}/>:<FourTab changeZ={props.changeZ} revertZ={props.revertZ}/>
    )
}

export default Classifier;



