import React from 'react';
import ReactDOM from 'react-dom';

import LightBox from './lightbox';

const OneTab=(props)=>{
    return(
        <div>
            <LightBox changeZ={props.changeZ} revertZ={props.revertZ} position={window.innerHeight*0.25*0}/>
            <LightBox changeZ={props.changeZ} revertZ={props.revertZ} position={window.innerHeight*0.25*1}/>
            <LightBox changeZ={props.changeZ} revertZ={props.revertZ} position={window.innerHeight*0.25*2}/>
            <LightBox changeZ={props.changeZ} revertZ={props.revertZ} position={window.innerHeight*0.25*3}/>
        </div>
    )
}

export default OneTab;


