import React from 'react';
import ReactDOM from 'react-dom';
import './tabs.css';
import {css} from 'glamor';

import LightBox from './lightbox';
import Classifier from './classifier';

class Tab extends React.Component{
    
    constructor(props){
        super(props);
        this.state={
            tabNames:['For You','Houses','Market Place','Services'],
            underLine:{ 
                width: '130%', height: '2px', backgroundColor: 'aqua',position: 'absolute',bottom: '-4px',transition: 'all 0.5s',left:'-15%'
            },
            opacIndex:[1,0,0,0],
            textColor:['aqua','black','black','black'],
            contIndex:[0,-1,-1,-1],
            contOpac:[1,0,0,0],
            windowWidth:window.innerWidth,
            tabContainerDisplay:{display:'block'},
        }
    }

    tabSelect=(data,e)=>{
        console.log(data)
        return this.setState(()=>({
                opacIndex:this.state.opacIndex.map((index,i)=>{
                    return (i===data)?1:0;
                }),
                textColor:this.state.textColor.map((color,i)=>{
                    return (i===data)?'aqua':'black';
                }),
                contIndex:this.state.contIndex.map((cont,i)=>{
                    return (i<data)?-1:(i===data)?0:-1;
                }),
                contOpac:this.state.contOpac.map((opac,i)=>{
                    return (i===data)?1:0;
                })
            }));
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
     }

     componentWillUnmount(){
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll=()=>{
        return this.setState(()=>({
            tabContainerDisplay:{display:'none'}
        }));
    }

    render(){
        return(
            <div className="tabWrapper">
                <div className="tabContainer" >
                        {
                            this.state.tabNames.map((tab,i)=>{
                                return(
                                    <div className="tabs" key={i}  {...css({color:this.state.textColor[i]})} onClick={this.tabSelect.bind(this,i)}>
                                            {tab}
                                            <div {...css(this.state.underLine)} {...css({opacity:this.state.opacIndex[i]})} />    
                                    </div>
                                );
                            })
                        }
                </div>
                <div className="contentContainer">
                    {   
                        this.state.tabNames.map((tab,i)=>{
                                    return(
                                            <div className="content" key={i} style={{left:this.state.windowWidth*this.state.contIndex[i],top:'0px',opacity:this.state.contOpac[i]}}>
                                                <Classifier value={i} changeZ={this.changeZ} revertZ={this.revertZ}/>
                                            </div>
                                    );
                                })
                    }
                </div>
            </div>
            )
    }
}

export default Tab;

