import React from 'react';
import ReactDOM from 'react-dom';
import './stickytab.css';
import {css} from 'glamor';

class StickyTab extends React.Component{

    constructor(props){
        super(props);
        this.state={

        }
    }

    render(){
        return(
            <div className="stickyContainer" >
                <div className="leftComponents">
                    <div className="stickyComponents" id={0} style={{float:'left'}}>
                            <i className="material-icons" style={{verticalAlign:'middle',padding:'5px 0 5px 0'}}>face</i>
                    </div>
                    <div className="stickyComponents" id={1} style={{float:'left'}}>
                            <div className="selectDrop">
                                <label>
                                    <select>
                                        <option>Option 1</option>
                                        <option>Option 2</option>
                                        <option>Krikiri</option>
                                        <option>Option 4</option>
                                    </select>
                                </label>
                            </div>
                    </div>
                    <div className="stickyComponents" id={2} style={{float:'left'}}>
                            <input type="text" placeholder="Search" ></input>
                            <i className="material-icons" style={{float:'left',color:'black',opacity:0.2,verticalAlign:'middle',padding:'5px 0 5px 0'}}>search</i>
                    </div>
                </div>
                <div className="rightComponents">
                    <div className="stickComponent" id={3} style={{float:'right'}}>
                            <p>Login</p>
                    </div>
                </div>
            </div>
        )
    }
}

export default StickyTab;


